body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-header {
  text-align: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}



.prevwSect > h3 {
  margin: 0;
  text-align: center;
  margin: 1vmin;
  color: white;
}

.previewer{
  display: flex;
  flex-flow: row wrap;
}

#editor{
  max-width: 95vw;
  width: 500px;
  height: 70vh;
  margin: 5vmin;
  margin: 0 5vmin;
  padding: 2px;
  border: solid black 1px;
}

#preview{
  max-width: 95vw;
  width: 500px;
  height: 70vh;
  color: black;
  background-color: white;
  margin: 0 5vmin;
  padding: 2px;
  border: solid black 1px;
  overflow: scroll;
}


